import React from 'react';
import './App.css';
import Home from "./Home"
import Bio from "./Bio"
import Projects from "./Projects"
import {HashRouter, Route, Switch} from 'react-router-dom'
import 'fontsource-roboto';
import { makeStyles } from '@material-ui/core/styles';
import { createMuiTheme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import MenuIcon from '@material-ui/icons/Menu';
import HomeIcon from '@material-ui/icons/Home';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import LibraryBooksIcon from '@material-ui/icons/LibraryBooks';

const MuiTheme = createMuiTheme({
    light: '#A3A3A3',
    main: '#828282',
    dark: '#686868',
    contrastText: '#fff'
});

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  title: {
    flexGrow: 1,
  },
}));

function App() {

  const classes = useStyles();

  
  document.body.style = 'background: #686868;';
  


  return (
      <>
          <div className={classes.root}>
            <AppBar position="static" style={{ background: MuiTheme.main }}>
              <Toolbar >
                <Button edge="start"><MenuIcon style={{ color: MuiTheme.contrastText }}/></Button>
                <Typography variant="h6" className={classes.title}>
                  Ian Bonafede
                </Typography>
                <Button href="/ianbonafede/#/" ><HomeIcon style={{ color: MuiTheme.contrastText }}/></Button>
                <Button href="/ianbonafede/#/bio"><AccountCircleIcon style={{ color: MuiTheme.contrastText }}/></Button>
                <Button href="/ianbonafede/#/projects"><LibraryBooksIcon style={{ color: MuiTheme.contrastText }}/></Button>
              </Toolbar>
            </AppBar>
          </div>

          <HashRouter basename={`${process.env.PUBLIC_URL}`} >
            <Switch>

            
              <Route exact path="/" component={Home}/>
              <Route exact path="/bio" component={Bio}/>
              <Route exact path="/Projects" component={Projects}/>
              
            </Switch>
          </HashRouter>
      </>

  );
  


}



export default App;
